export class ListComponent implements OnInit {
  @Input('sem-theme')
  public semuiTheme: ThemeImportanceLevel;
  @HostBinding('class') @Input('class') classes: string;

  /**
   * Bind to the host "light" CSS class.
   */
  @HostBinding('class.sem-ul-light')
  public styleAsLightTheme: boolean;

  /**
   * Bind to the host "dark" CSS class.
   */
  @HostBinding('class.sem-ul-dark')
  public styleAsDarkTheme: boolean;

  constructor() {
  }

  ngOnInit() {
    this.addClasses( this.getClasses() )
    this.styleElementTheme(this.semuiTheme);
  }


  addClasses(classes: string[]) {
    const spacer = this.classes ? '' : '';
    const base = ' sem-ul--';
    classes.forEach((_class, index) => {
      if (index === 0) {
        this.classes += spacer + base + _class;
      } else {
        this.classes += ' ' + base + _class;
      }
    });
  }
  getClasses() {
    const _classes = [];
    _classes.push(' sem-ul');
    return _classes;
  }
  private styleElementTheme(theme){
    switch (theme) {
      case ('light'):
        this.styleAsLightTheme = true;
        break;
      case ('dark'):
        this.styleAsDarkTheme = true;
        break;
      default:
    }
  }
}
