import {
  ChangeDetectionStrategy, ChangeDetectorRef,
  Component, ElementRef,
  EventEmitter, HostBinding,
  Input, OnChanges, OnDestroy, OnInit,
  Output, SimpleChanges, ViewChild
} from '@angular/core';

import {
  CdkOverlayOrigin,
  Overlay
} from '@angular/cdk/overlay';
import { Subject } from 'rxjs/Subject';
import { Observable} from 'rxjs/Observable';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/do';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'semui-overlay-dialog',
  template: ` <ng-template
    cdkConnectedOverlay
    [cdkConnectedOverlayOrigin]="overlayOrigin"
    [cdkConnectedOverlayOpen]="isOpened"
    [cdkConnectedOverlayMinWidth]="overlayWidth"
    (detach)="connectedOverlayDetach()"
  >
    <div #dialog role="dialog" aria-label="Dialog" class="dialog-container">
      <ng-content select="[overlay-title]"></ng-content>
      <ng-content></ng-content>
    </div>
  </ng-template>
`
})
export class OverlayDialogComponent implements  OnDestroy, OnInit  {
  constructor(
    public overlay: Overlay,
    private changeDetectorRef: ChangeDetectorRef
  ) {}

  @Input() overlayOrigin: CdkOverlayOrigin;
  @Input() isOpened = false;
  @Input() overlayWidth: string;
  @Output() close = new EventEmitter<any>();
  @Output() open = new EventEmitter<any>();

  @ViewChild('dialog') dialog: ElementRef;
  destroy$ = new Subject<any>();
  //
  // overlayOffsetX;
  // OverlayHeight;
  // scrollStrategy;
  // positions;
  // backdropClass;

  ngOnInit(): void {

    //   this.backdropClass = 'cdkOverlayFullHeight'
    //   this.overlayOffsetX = 0;
    //   this.OverlayHeight = '100%';
    //   this.positions  = [
    //     new ConnectionPositionPair(
    //       {originY: 'center', originX: 'end'},
    //       {overlayX: 'start', overlayY: 'top'}
    //     )
    //   ];
    //   this.scrollStrategy = this.overlay.scrollStrategies.block();


    const overlayOriginEl = this.overlayOrigin.elementRef.nativeElement;



    const close$ = Observable.fromEvent(document, 'click')
      .debounceTime(100)
      .filter(() => this.isOpened)
      .filter(event => {

        return this.isClickedOutside(overlayOriginEl, this.dialog, event)
      })
      .do( (data) => {
        this.changeState(false);
      })
    close$.subscribe();
  }
  connectedOverlayDetach() {
    this.changeState(false);
  }
  ngOnDestroy(): void {
    this.destroy$.next();
  }

  private changeState(isOpened: boolean) {
    this.isOpened = isOpened;
    isOpened ? this.open.emit() : this.close.emit();
    this.changeDetectorRef.markForCheck();
  }
  private isClickedOutside(overlayOriginEl, dialog, event): boolean {
    return !(overlayOriginEl.contains(event['target']) || dialog.nativeElement.contains(event['target']));
  }



}
